import React from 'react'
import PropTypes from 'prop-types'

import Moment from 'react-moment'

import { faClock } from '@fortawesome/free-regular-svg-icons'

import FAIcon from '@src-components/block/fa-icon'
import ScreenReaderText from '@src-components/block/screen-reader-text'

const MetaItemPublishTime = props => {
	const { item } = props
	if (!item) return null

	return (
		<div className="inline-block mb-1 last:mb-0 ml-4 first:ml-0">
			<div className="flex items-start text-gray-600">
				<dt>
					<FAIcon icon={faClock} size="sm" />
					<ScreenReaderText>公開日時</ScreenReaderText>
				</dt>
				<dd>
					<Moment
						date={item}
						format="YYYY年MM月DD日 hh:mm"
						className="inline-block ml-1 text-sm"
					/>
				</dd>
			</div>
		</div>
	)
}

MetaItemPublishTime.propTypes = {
	item: PropTypes.node,
}

export default MetaItemPublishTime
