import React from 'react'
import PropTypes from 'prop-types'
import Moment from 'react-moment'

import { faHistory } from '@fortawesome/free-solid-svg-icons'

import FAIcon from '@src-components/block/fa-icon'
import ScreenReaderText from '@src-components/block/screen-reader-text'

const MetaItemModifiedTime = props => {
	const { item } = props
	if (!item) return null

	return (
		<div className="inline-block mb-1 last:mb-0 ml-4 first:ml-0">
			<div className="flex items-start text-gray-600">
				<dt>
					<FAIcon icon={faHistory} size="sm" />
					<ScreenReaderText>更新日時</ScreenReaderText>
				</dt>
				<dd>
					<Moment
						date={item}
						format="YYYY年MM月DD日 hh:mm"
						className="inline-block text-sm ml-1"
					/>
				</dd>
			</div>
		</div>
	)
}

MetaItemModifiedTime.propTypes = {
	item: PropTypes.node,
}

export default MetaItemModifiedTime
