import React, { useState, useRef } from 'react'
import { graphql } from 'gatsby'
import PropTypes from 'prop-types'

import AppContext from '@src-contexts/app-context'
import SEO from '@src-components/common/seo'

import Layout from '@src-components/layout'

import MetaItems, {
	MetaItemCategories,
	MetaItemTags,
	MetaItemPublishTime,
	MetaItemModifiedTime,
} from '@src-components/parts/meta-items'

import ArticleContent from '@src-components/parts/article-content'
import PageHeading from '@src-components/parts/page-heading'

import PrevNext from '@src-components/parts/prev-next'
import RelatedPosts from '@src-components/parts/related-posts'
import RelatedHooks from '@src-components/parts/related-hooks'

import { faNewspaper } from '@fortawesome/free-regular-svg-icons'
import { faHome, faListAlt } from '@fortawesome/free-solid-svg-icons'

const PostSingleTemplate = props => {
	const { location, data, pageContext } = props

	const { body, tableOfContents, frontmatter } = data.mdx
	const { prevPostId, nextPostId, relatedPosts, relatedHooks } = pageContext

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)
	const [isOpenTOC, setIsOpenTOC] = useState(false)
	const activeTOC = useRef(null)

	const pageTitle = frontmatter.title
	const pageDescription = frontmatter.description

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		tocItems: tableOfContents.items,
		breadcrumbItems: [
			{ label: 'ホーム', url: '/', icon: faHome },
			{ label: '記事一覧', url: '/posts/', icon: faListAlt },
			{ label: frontmatter.title, url: location.pathname, icon: faNewspaper },
		],
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
		isOpenTOC: isOpenTOC,
		setIsOpenTOC: setIsOpenTOC,
		activeTOC: activeTOC,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout>
				<PageHeading>{pageTitle}</PageHeading>
				{/* メタ項目 */}
				<MetaItems>
					<MetaItemModifiedTime item={frontmatter.modifiedTime} />
					<MetaItemPublishTime item={frontmatter.publishTime} />
					<MetaItemCategories items={frontmatter.categories} />
					<MetaItemTags items={frontmatter.tags} />
				</MetaItems>
				{/* 記事コンテンツ */}
				<ArticleContent>{body}</ArticleContent>
				<aside>
					{/* 前後ナビ */}
					<PrevNext prevId={prevPostId} nextId={nextPostId} />
					{/* 関連コンテンツ */}
					<RelatedPosts items={relatedPosts} />
					{/* 関連フック */}
					<RelatedHooks items={relatedHooks} />
				</aside>
			</Layout>
		</AppContext.Provider>
	)
}

PostSingleTemplate.propTypes = {
	data: PropTypes.object,
	pageContext: PropTypes.object,
}

export default PostSingleTemplate

export const pageQuery = graphql`
	query($id: String!) {
		mdx(id: { eq: $id }) {
			id
			body
			tableOfContents
			frontmatter {
				title
				description
				thumbnail
				categories
				tags
				publishTime
				modifiedTime
			}
			fields {
				slug
				name
			}
		}
	}
`
