import React from 'react'
import Moment from 'react-moment'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import { faHistory } from '@fortawesome/free-solid-svg-icons'
import { faClock, faNewspaper } from '@fortawesome/free-regular-svg-icons'

import FAIcon from '@src-components/block/fa-icon'

import { ThumbnailImage } from '@src-components/common/image'

import SectionHeading from '@src-components/parts/section-heading'

const RelatedPosts = props => {
	const { items } = props
	return (
		<section className="first:mt-16 border-gray-200 border-t-2 py-4">
			<SectionHeading icon={faNewspaper}>関連記事</SectionHeading>
			<div className="mt-4">
				{items.length > 0 ? (
					<ul className="grid grid-cols-1 gap-y-8 lg:grid-cols-2 lg:gap-x-5 lg:gap-y-6">
						{items.map(item => {
							return (
								<li key={item.slug}>
									<Link to={item.slug} className="block group">
										<figure>
											<div className="relative rounded overflow-hidden transition transform duration-150 ease-in-out">
												<ThumbnailImage
													filePath={item.thumbnail}
													alt={item.title}
													className="w-full h-auto"
												/>
												<div className="absolute inset-0 flex items-center justify-center text-center border border-gray-200 rounded-md opacity-15 transition ease-in-out duration-150"></div>
												<div className="absolute inset-0 bg-gray-50 opacity-0 group-hover:opacity-50 transition ease-in-out duration-150"></div>
											</div>
											<figcaption className="mt-2 group-hover:bg-gray-50 p-2 rounded-md">
												<p className="text-sm font-medium text-gray-900">
													{item.title}
												</p>
												<p className="text-sm text-gray-500">
													{item.description}
												</p>
												<div>
													{item.modifiedTime && (
														<div className="inline-block ml-4 first:ml-0">
															<p className="text-sm text-gray-900 flex items-center">
																<FAIcon
																	icon={faHistory}
																	size="sm"
																	className="mr-1"
																/>
																<Moment
																	date={item.modifiedTime}
																	format="YYYY年MM月DD日"
																	className="inline-block"
																/>
															</p>
														</div>
													)}
													<div className="inline-block ml-4 first:ml-0">
														<p className="text-sm text-gray-900 flex items-center">
															<FAIcon
																icon={faClock}
																size="sm"
																className="mr-1"
															/>
															<Moment
																date={item.publishTime}
																format="YYYY年MM月DD日"
																className="inline-block"
															/>
														</p>
													</div>
												</div>
											</figcaption>
										</figure>
									</Link>
								</li>
							)
						})}
					</ul>
				) : (
					<p className="mt-4 leading-tight">関連記事はありません。</p>
				)}
			</div>
		</section>
	)
}

RelatedPosts.propTypes = {
	relatedPosts: PropTypes.array,
}

export default RelatedPosts
