import React from 'react'
import Moment from 'react-moment'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import {
	faRunning,
	faFilter,
	faAnchor,
} from '@fortawesome/free-solid-svg-icons'

import FAIcon from '@src-components/block/fa-icon'

import SectionHeading from '@src-components/parts/section-heading'

const RelatedHooks = props => {
	const { items } = props
	return (
		<section className="first:mt-16 border-gray-200 border-t-2 py-4">
			<SectionHeading icon={faAnchor}>
				この記事内で使用されているフック
			</SectionHeading>
			<div className="mt-4">
				{items.length > 0 ? (
					<ul className="grid grid-cols-1 gap-y-8 lg:grid-cols-2 lg:gap-x-5 lg:gap-y-6">
						{items.map(item => {
							return (
								<li key={item.slug}>
									<Link
										to={item.slug}
										className="block mt-2 bg-gray-100 hover:bg-gray-200 p-2 rounded"
									>
										<p className="text-sm font-medium text-gray-900">
											{item.title}
										</p>
										<p className="text-sm text-gray-500">{item.description}</p>
										<div className="mt-2">
											<p className="text-sm text-gray-900 flex items-center justify-end">
												<FAIcon
													icon={
														item.hookType === 'action' ? faRunning : faFilter
													}
													className="mr-1"
												/>
												{item.hookType === 'action' ? (
													<ruby>
														Action Hooks<rt>アクション フック</rt>
													</ruby>
												) : (
													<ruby>
														Filter Hooks<rt>フィルター フック</rt>
													</ruby>
												)}
											</p>
										</div>
									</Link>
								</li>
							)
						})}
					</ul>
				) : (
					<p className="mt-4 leading-tight">
						この記事内で使用されているフックはありません。
					</p>
				)}
			</div>
		</section>
	)
}

RelatedHooks.propTypes = {
	relatedPosts: PropTypes.array,
}

export default RelatedHooks
