/**
 * 前後ナビ
 */
import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'
import { css } from '@emotion/core'

import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons'

import FAIcon from '@src-components/block/fa-icon'

import { AllPostContentQuery } from '@src-queries'

import { ThumbnailImage } from '@src-components/common/image'

const PrevNext = ({ prevId, nextId }) => {
	// 全記事データの取得
	const allPostContentData = AllPostContentQuery()
	// 前記事の取得
	const prevPostContentData = allPostContentData.find(
		postContentData => postContentData.node.id === prevId
	)
	// 次記事の取得
	const nextPostContentData = allPostContentData.find(
		postContentData => postContentData.node.id === nextId
	)
	// 前後ナビの生成
	return (
		<section className="flex flex-row justify-between my-4 w-full border-gray-200 border-t-2 first:mt-16">
			<div className="flex self-start justify-start w-1/2 h-full m-2">
				{prevPostContentData && (
					<Link
						to={prevPostContentData.node.fields.slug}
						className="block group w-full relative"
					>
						<figure>
							<div className="relative rounded overflow-hidden transition transform duration-150 ease-in-out">
								<ThumbnailImage
									filePath={prevPostContentData.node.frontmatter.thumbnail}
									alt={prevPostContentData.node.frontmatter.title}
									className="w-full h-auto"
								/>
								<div className="absolute inset-0 flex items-center justify-center text-center border border-gray-200 rounded-md opacity-15 transition ease-in-out duration-150"></div>
								<div className="absolute inset-0 bg-gray-50 opacity-0 group-hover:opacity-50 transition ease-in-out duration-150"></div>
							</div>
							<figcaption className="mt-2 group-hover:bg-gray-50 p-2 rounded-md">
								<span
									className="absolute z-10 bg-white group-hover:bg-gray-50 group-hover:text-gray-900 border-gray-200 border-2 rounded text-sm font-semibold px-1"
									css={css`
										left: 0.5rem;
										top: -1.5rem;
									`}
								>
									<FAIcon icon={faAngleLeft} className="mr-1" />
									新しい記事
								</span>

								<p className="text-sm font-medium text-gray-900">
									{prevPostContentData.node.frontmatter.title}
								</p>
							</figcaption>
						</figure>
					</Link>
				)}
			</div>
			<div className="flex self-start justify-end w-1/2 h-full m-2">
				{nextPostContentData && (
					<Link
						to={nextPostContentData.node.fields.slug}
						className="block group w-full relative"
					>
						<figure>
							<div className="relative rounded overflow-hidden transition transform duration-150 ease-in-out">
								<ThumbnailImage
									filePath={nextPostContentData.node.frontmatter.thumbnail}
									alt={nextPostContentData.node.frontmatter.title}
									className="w-full h-auto"
								/>
								<div className="absolute inset-0 flex items-center justify-center text-center border border-gray-200 rounded-md opacity-15 transition ease-in-out duration-150"></div>
								<div className="absolute inset-0 bg-gray-50 opacity-0 group-hover:opacity-50 transition ease-in-out duration-150"></div>
							</div>
							<figcaption className="mt-2 group-hover:bg-gray-50 p-2 rounded-md">
								<span
									className="absolute z-10 bg-white group-hover:bg-gray-50 group-hover:text-gray-900 border-gray-200 border-2 rounded text-sm font-semibold px-1"
									css={css`
										right: 0.5rem;
										top: -1.5rem;
									`}
								>
									古い記事
									<FAIcon icon={faAngleRight} className="ml-1" />
								</span>
								<p className="text-sm font-medium text-gray-900">
									{nextPostContentData.node.frontmatter.title}
								</p>
							</figcaption>
						</figure>
					</Link>
				)}
			</div>
		</section>
	)
}

PrevNext.propTypes = {
	prevId: PropTypes.string,
	nextId: PropTypes.string,
}

export default PrevNext
